.calculatorTotal {
  margin: 50px 40px 0 30px;
  display: flex;
  justify-content: space-between;
}

.calculatorTotal h2 {
  font-size: 2.2rem;
}

.container {
  width: 40%;
}

.calculator {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.display {
  width: 100%;
  height: 60px;
  background: #80808c;
  color: white;
  padding: 20px 10px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.display p {
  font-size: 1.2rem;
}

.buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.buttonsCalculate {
  width: 25%;
  height: 25%;
  padding: 16px 0;
  background-color: #e6e6e6de;
  border: 0.1px solid lightgray;
  font-size: 1rem;
}

.buttonsCalculate:nth-child(17) {
  width: 50%;
}

.buttonsCalculate:nth-child(4),
.buttonsCalculate:nth-child(8),
.buttonsCalculate:nth-child(12),
.buttonsCalculate:nth-child(16),
.buttonsCalculates:nth-child(19) {
  background-color: #ff7600d4;
}